.mbti-layout {
    width: 100vw;
    font-size: 14px;
    display: flex;
    justify-content: center;
    background-color: #eee;
    height: calc(var(--vh, 1vh) * 100);
}
.start-page-layout {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #ffea35;
    width: 100%;
    max-width: 900px;
    height: 100%;
    color: #413730;
}
.start-logo {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 25px;
    font-weight: 700;
}
.start-logo div:nth-child(1) {
    background-color: #413730;
    color: #ffea35;
    width: 100px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
}
.start-logo div:nth-child(2) {
    margin-top: -15px;
}
.start-button {
    font-size: 17px;
    font-weight: 700;
    margin-top: 20px;
}

.question-layout {
    display: flex;
    flex-direction: column;
    background-color: #bacee0;
    width: 100%;
    max-width: 900px;
    height: 100%;
    overflow: hidden;
}
.mbti-title {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    padding: 15px;
    box-sizing: border-box;
    font-weight: 700;
}
.mbti-title div:nth-child(1) {
    display: flex;
    flex: 1; /* 빈공간 가득 채움 */
}

.question-list {
    flex-direction: column;
    height: 100%;
    overflow: scroll;
    scrollbar-width: none;
}
.question-list::-webkit-scrollbar {
    display: none;
}

.question-item-layout {
    display: flex;
    flex: 1;
    margin: 10px 15px 5px 15px;
}
.answer-item-layout {
    width: 100%;
    background-color: #fff;
    padding-bottom: 10px;
    display: flex;
    flex-direction: column;
}
.profileImg {
    display: flex;
    flex-direction: column;
    width: 40px;
    height: 40px;
    background-color: #a1b6e9;
    border-radius: 15px;
    margin-right: 5px;
    align-items: center;
    justify-content: center;
}
.profileImg div:nth-child(1) {
    background-color: #cbd6f2;
    width: 8px;
    height: 8px;
    border-radius: 4px;
}
.profileImg div:nth-child(2) {
    background-color: #cbd6f2;
    width: 16px;
    height: 8px;
    border-radius: 8px 8px 0px 0px;
    margin-top: 2px;
}
.chat-list-layout {
    display: flex;
    flex-direction: column;
}
.chat-box {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    max-width: 60vw;
    margin: 3px 0px 12px 0px;
}
.chat-box div:nth-child(1) {
    color: #fff;
    font-size: 12px;
    margin-top: 8px;
}
.chat-box div:nth-child(2) {
    padding: 7px 10px 7px 10px;
    background-color: #fff;
    margin-left: -4px;
    border-radius: 10px;
    text-align: left;
}
.aChatBox {
    display: flex;
    width: 100%;
    color: #969696;
    font-size: 22px;
    border-bottom: 1px solid #eee;
    margin-bottom: 25px;
}
aChatBox div:nth-child(1) {
    display: flex;
    flex: 1;
    padding: 10px;
    box-sizing: border-box;
}
aChatBox div:nth-child(2) {
    padding: 10px;
}
.answer-box {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
    text-align: center;
    box-shadow: 2px 2px 0px #ededed;
    border: 1px solid #ededed;
    margin: 0px 20px 15px 20px;
    border-radius: 10px;
}
